let BookServiceService = class BookServiceService {
    constructor(http) {
        this.http = http;
        this.booksUrl = 'http://localhost:3000/api/books';
    }
    getBooks() {
        return this.http.get(this.booksUrl)
            .toPromise()
            .then(response => response.json())
            .catch(this.handleError);
    }
    getSingleBook(bookId) {
        return this.http.get(this.booksUrl + '/' + bookId)
            .toPromise().then(response => response.json())
            .catch(this.handleError);
    }
    getSingleReview(bookId, reviewId) {
        return this.http.get(this.booksUrl + '/' + bookId + '/reviews/' + reviewId)
            .toPromise().then(response => response.json())
            .catch(this.handleError);
    }
    createBook(newBook) {
        return this.http.post(this.booksUrl, newBook)
            .toPromise().then(response => response.json())
            .catch(this.handleError);
    }
    updateBook(bookId, theBook) {
        return this.http.put(this.booksUrl + '/' + bookId, theBook)
            .toPromise()
            .then(response => response.json())
            .catch(this.handleError);
    }
    deleteBook(bookid) {
        return this.http.delete(this.booksUrl + '/' + bookid)
            .toPromise()
            .then(response => response.json())
            .catch(this.handleError);
    }
    addReviewByReviewId(bookId, formData) {
        const url = `${this.booksUrl}/${bookId}/reviews`;
        return this.http
            .post(url, formData)
            .toPromise()
            .then(response => response)
            .catch(this.handleError);
    }
    updateReviewByReviewId(bookId, reviewId, formData) {
        const url = `${this.booksUrl}/${bookId}/reviews/${reviewId}`;
        return this.http
            .put(url, formData)
            .toPromise()
            .then(response => response)
            .catch(this.handleError);
    }
    deleteReviewByReviewId(bookId, reviewId) {
        const url = `${this.booksUrl}/${bookId}/reviews/${reviewId}`;
        return this.http.delete(url).toPromise().then(response => response.json()).catch(this.handleError);
    }
    handleError(error) {
        console.log("error");
    }
};
